import { Component } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { filter } from 'rxjs/operators';
import { Title } from '@angular/platform-browser';
import { TranslationService } from './services/translation.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'sheikh';
  pathname;
  tabs: Object[] = [];
  activeTab: string;
  value: string;

  constructor(private router: Router, private _route: ActivatedRoute, private translate: TranslationService, private titleService: Title) {
    this.router.events.pipe(
      filter(e => e instanceof NavigationEnd)
    ).subscribe((e: NavigationEnd) => {
      this.pathname = location.hash.split('/');
      this.pathname = this.pathname[1].includes('?') ? this.pathname[1].split('?')[0] : this.pathname[1]
      this.activeTab = this.pathname
      this.setTitle(this.pathname);
      this.tabs = ['', 'about', 'schedules', 'instructors', 'archive', 'albums', 'start-learning', 'FAQs']
    });
  }

  setTitle(newTitle: string) {
    newTitle === ''
    ? this.titleService.setTitle('شيخ العمود')
    : this.titleService.setTitle( this.translate.translate(newTitle) + ' - شيخ العمود' );
  }

  search() {
    this.router.navigate(['/search'], { queryParams: {search: this.value}, queryParamsHandling: 'merge',})
  }
}
