import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-learn',
  templateUrl: './learn.component.html',
  styleUrls: ['./learn.component.scss']
})
export class LearnComponent implements OnInit {
  halls;
  names = [
    {name: 'الأول'},
    {name: 'الثاني'},
    {name: 'الثالث'},
    {name: 'الرابع'},
    {name: 'الخامس'},
    {name: 'السادس'},
    {name: 'السابع'},
    {name: 'الثامن'},
    {name: 'التاسع'},
    {name: 'العاشر'},
    {name: 'الحادي عشر'},
    {name: 'الثاني عشر'},
  ];
  hallsAreReady: boolean = false;
  days = [
    {name: 'all', uid: 0, selected: false, courses: []},
    {name: 'friday', selected: false, courses: []},
    {name: 'saturday', selected: false, courses: []},
    {name: 'sunday', selected: false, courses: []},
    {name: 'monday', selected: false, courses: []},
    {name: 'tuesday', selected: false, courses: []},
    {name: 'wednesday', selected: false, courses: []},
    {name: 'thursday', selected: false, courses: []},
  ];

  colors = ['green', 'blue', 'black', 'gray'];
  colorsSet: boolean = false;

  active: number = 0;

  constructor(private http: HttpClient) {
  }

  ngOnInit() {
    this.getRwaq();
  }

  async getRwaq(id = "1") {
    this.halls = await this.http.get('https://sheikhalamoud.org/apis/sessions/', {params: {
      rowaq_id: id.toString(),
    }}).toPromise();
    this.halls.map(this.cbGroupCoursesByDays);
    this.colorsSet = true;

    this.hallsAreReady = true;
  }

  cbGroupCoursesByDays = course => {
    if (!this.colorsSet)
      course.color = this.colors[Math.floor(Math.random()*4)];
    course['days'].map((courseDay: string) => {
      this.days.map(day => {
        if (day.name === courseDay) day.courses.push(course);
      });
    });
  }

}
