import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'header-svg',
  templateUrl: './header-svg.component.html',
  styleUrls: ['./header-svg.component.scss']
})
export class HeaderSvgComponent implements OnInit {
  @Input() text: string;
  @Input() color: string;
  @Input() index: string;
  title: string[];
  @ViewChild('injtext', {static: false}) injtext: ElementRef;
  @ViewChild('svg', {static: false}) svg: ElementRef;
  
  constructor() {}
  
  ngOnInit() {}
  
  ngAfterViewInit() {
    this.title = this.text.split(' ')
    let hw: number; // elm half width
    let x: number;
    let halfs: number;
    let text = this.injtext.nativeElement;
    const allSVG = this.svg.nativeElement.getBoundingClientRect().width;
    const halfSVG = this.svg.nativeElement.getBoundingClientRect().width/2;
    const SVGBox = this.svg.nativeElement.getBBox().width;

    text.innerHTML += `<text y="46%" style="font-size: 1.2rem;" id="${this.index}processing">${this.title[0]}</text>`;
    setTimeout(() => {
      let processing = document.getElementById(`${this.index}processing`);
      hw = processing.getBoundingClientRect().width/2;
      halfs = (halfSVG + hw);
      x = halfs * SVGBox / allSVG;
      processing.setAttribute('x', x.toString());
  
      text.innerHTML += `<text y="58%" id="${this.index}rest">${this.title.slice(1).join(' ')}</text>`;
      setTimeout(() => {
        let rest = document.getElementById(`${this.index}rest`);
        hw = rest.getBoundingClientRect().width/2;
        halfs = (halfSVG + hw);
        x = halfs * SVGBox / allSVG;
        rest.setAttribute('x', x.toString());
      }, 0);
    }, 0);
  }

}
