import { Component, OnInit, Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-instructors',
  templateUrl: './instructors.component.html',
  styleUrls: ['./instructors.component.scss']
})
export class InstructorsComponent implements OnInit {
  @Input() inInstructors;
  instructors;
  currentInstructors: Object[] = [];
  pastInstructors: Object[] = [];
  instructorsReady: boolean = false;
  constructor(private http: HttpClient) {
  }
  
  async ngOnInit() {
    this.instructors = !this.inInstructors
      ? await this.http.get('https://sheikhalamoud.org/apis/instructors').toPromise()
      : this.inInstructors
    this.currentInstructors = this.instructors.filter(instructor => instructor['status'] === "current");
    this.currentInstructors.sort(function(a, b) { 
      return a['order'] - b['order'];
    });
    this.pastInstructors = this.instructors.filter(instructor => instructor['status'] === "past");
    this.pastInstructors.sort(function(a, b) { 
      return a['order'] - b['order'];
    });
    this.instructorsReady = true;
  }

}
