import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-instructor',
  templateUrl: './instructor.component.html',
  styleUrls: ['./instructor.component.scss']
})
export class InstructorComponent implements OnInit {
  instructor;
  instructorIsReady: boolean = false;
  
  constructor(private http: HttpClient) {
  }

  async ngOnInit() {
    const id = location.hash.split('/')[2];
    this.instructor = await this.http.get(`https://sheikhalamoud.org/apis/instructors/${id}`).toPromise();
    this.instructor.description = this.instructor.description.replace(/(?:\\[rn]|[\r\n]+)+/g, "\n");

    this.instructorIsReady = true;
  }

}
