import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

interface Doc {
  SystemDescription: string,
  SystemId: number,
  SystemName: string,
  SystemVisible: number,
}

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})

export class AboutComponent implements OnInit {
  systems;
  ActiveIdx: number = 0;
  ActiveDescription: string;
  instructor: Object;
  main_desc: string = `\n<span style="font-weight: 400;">المقصد</span>\nإنطلاقا من رؤيتنا لشمولية مقاصد الإسلام عقيدة و شريعة و تزكية و عمرانا، لذلك راعينا أن نضم مختلف ألوان العلوم الشرعية والإنسانية والتطبيقية لإحداث تكاملية بين العلوم، وأن يتم التعليم بوسائل تخاطب العقل والوجدان معا، وتلبي احتياجات واقع الأمة، علي أيدي متخصصين، وبأسس منهجية وعلمية، كما تستهدف مدرسة شيخ العمود أيضا إعادة التواصل المفقود بين علماء الأمة و شبابها، والذي كان فقده نكالا علي الطرفين .\n\n<span style="font-weight: 400;">الرسالة</span>\nخدمة العلم لنهضة الأمة.\n\n<span style="font-weight: 400;">الرؤية</span>\nنعمل على تغطية مختلف دوائر العلوم (الشرعية واللغوية والإنسانية والطبيعية) في جميع مسارات التعليم والتثقيف بالمدرسة (الدورات والمجالس والندوات والكتاب والأطفال)  بنهاية عام 2020م، برعاية جيدة للقيم التعليمية الست (الحرية والتدرج والتكاملية والشغف والاحترام ومركزية الوحي) وذلك من خلال بيئة عمل جذابة وقيمية ومتجددة،	تراعي الاحترافية والتراحمية والريادة فى مجتمع التعليم العربى. \n\n<span style="font-weight: 400;">القيم الحاكمة</span>\nإننا نتحرك بمجموعة من القيم الحاكمة التي نرى أن في عودتها للمنظومة التعليمية صلاحا للدين والدنيا، وأهم هذه القيم ستة هي:\n- مركزية الوحي\n- الحرية\n- التكاملية\n- التدرج\n- الشغف\n- الاحترام\n\n<span style="font-weight: 400;">الاسم</span>\nاسم "شيخ العمود" يلقي في بالنا بصورة ذلك الشيخ العالِم عند عموده وطلابه الملتفين من حوله يتفاعل معهم ويأخذون عنه العلم والأخلاق معا.\n\n<span style="font-weight: 400;">العلوم</span>\nوالعلوم التي يتم تدرسيها في شيخ العمود تدور في أربع دوائر رئيسية هي:\nأولا: العلوم الشرعية (العقيدة، والفقه، والتزكية، وأصول الفقه، والمنطق، وعلوم القرآن، والتفسير، وعلوم الحديث،...إلخ.).\nثانيا: علوم اللغة العربية (النحو، والصرف، والبلاغة، والأدب، والعروض، والخط، والإملاء، .... إلخ).\nثالثا: العلوم الإنسانية (الاجتماع، علم النفس، الاقتصاد، الفلسفة، السياسة، الآداب، الفنون، ...إلخ).\nرابعا: العلوم الطبيعية (الطب، الصيدلة، النبات، الحيوان، الميكانيكا، الفيزياء، الكيمياء، الرياضيات، الفلك، ... إلخ).\n
  `;
  about: SafeHtml;

  constructor(private http: HttpClient, private router: Router, private sanitizer: DomSanitizer) { 
    this.about = this.sanitizer.bypassSecurityTrustHtml(this.main_desc);
  }

  async ngOnInit() {
    this.systems = await this.http.get('https://sheikhalamoud.org/apis/systems/').toPromise();
    this.systems.map(sys => sys.description = sys.description.replace(/(?:\\[rn]|[\r\n]+)+/g, "\n\n").replace(/\\"/g, '"'))
    this.ActiveDescription = this.systems[0]['description'];
    localStorage.setItem('payload', JSON.stringify({params: {system_ids: [this.systems[0]['uid']]}}));
  }

  changeTo(idx: number){
    this.ActiveIdx = idx;
    this.ActiveDescription = this.systems[idx]['description'];

    localStorage.setItem('payload', JSON.stringify({params: {system_ids: [this.systems[idx]['uid']]}}));
  }

  clearFiltersAndNavigate() {
    localStorage.setItem('selectedSystems', JSON.stringify([this.systems[this.ActiveIdx]['uid']]));
    localStorage.setItem('selectedLocations', JSON.stringify([0]));
    localStorage.setItem('selectedDays', JSON.stringify(["all"]));
    this.router.navigate(['schedules']);
  }
}
