import { Component, OnInit, Input, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-sessions',
  templateUrl: './sessions.component.html',
  styleUrls: ['./sessions.component.scss']
})
export class SessionsComponent implements OnInit {
  @Input() days;
  isGridView: boolean = true;
  isCurrentMonth: boolean = true;
  coursesNo: number = 0;

  constructor() {
    this.isGridView = !!localStorage.getItem('isGridView');
  }
  
  ngOnInit() {
  }
  
  ngOnChanges(changes: SimpleChanges) {
    this.coursesNo = 0;
    changes.days.currentValue.map(day => this.coursesNo += day.courses.length);
  }

  getTimeFrom(d: Date) {
    const date = new Date(d);
    let hours = date.getHours();
    const minutes = date.getMinutes();
    let stringHours: string;
    let stringMinutes: string;
    const ampm = hours >= 12 ? 'م' : 'ص';
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    stringHours = hours < 10 ? '0' + hours : hours.toString();
    stringMinutes = minutes < 10 ? '0' + minutes : minutes.toString();
    var strTime = stringHours + ':' + stringMinutes + ' ' + ampm;
    return strTime;
  }
  
  toggleGridView() {
    this.isGridView = !this.isGridView;
    localStorage.setItem('isGridView', this.isGridView ? "true" : "")
  }

  open(url: string) {
    window.open(url, '_blank')
  }

  upcomingMonth(): string {
    const now = new Date();
    now.setDate(1);
    now.setMonth(now.getMonth() + 1);
    return now.toLocaleDateString('ar', {month: 'long'});
  }

  currentMonth(): string {
    const now = new Date();
    return now.toLocaleDateString('ar', {month: 'long'});
  }

}
