import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-archive',
  templateUrl: './archive.component.html',
  styleUrls: ['./archive.component.scss']
})
export class ArchiveComponent implements OnInit {
  parents = JSON.stringify('parents')
  sciences;
  sciencesAreReady: boolean = false;

  constructor(private http: HttpClient) {
    this.getSciences();
  }

  ngOnInit() {
  }
  
  async getSciences() {
    this.sciences = await this.http.get('https://sheikhalamoud.org/apis/sciences/').toPromise();
    this.sciences.map((s, i) => {
      s.children = this.sciences.slice(i + 1).map(cs => cs.parent_uid === s.uid)
    })
    this.sciencesAreReady = true;
  }

}
