import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-schedules',
  templateUrl: './schedules.component.html',
  styleUrls: ['./schedules.component.scss']
})

export class SchedulesComponent implements OnInit {
  courses;
  coursesPerDay: Object[];
  selectedDays = [];
  selectedSystems = [];
  selectedLocations = [];
  areCoursesReady: boolean = false;
  payload;
  days = [
    {name: 'all', uid: 0, selected: false, courses: []},
    {name: 'friday', selected: false, courses: []},
    {name: 'saturday', selected: false, courses: []},
    {name: 'sunday', selected: false, courses: []},
    {name: 'monday', selected: false, courses: []},
    {name: 'tuesday', selected: false, courses: []},
    {name: 'wednesday', selected: false, courses: []},
    {name: 'thursday', selected: false, courses: []},
  ];
  systems;
  locations;
  filtersAreReady: boolean = false;
  collapsed: boolean = true;
  prepayload: string;
  filtered;
  nonFiltered;
  sysFiltered;
  locFiltered;

  colors = ['green', 'blue', 'black', 'gray'];
  colorsSet: boolean = false;


  constructor(private http: HttpClient) {
    this.selectedDays = JSON.parse(localStorage.getItem('selectedDays')) || this.selectedDays;
    this.selectedSystems = JSON.parse(localStorage.getItem('selectedSystems')) || this.selectedSystems;
    this.selectedLocations = JSON.parse(localStorage.getItem('selectedLocations')) || this.selectedLocations;
    this.prepayload = localStorage.getItem('payload') || this.prepayload;
    this.setFilters();
  }

  ngOnInit() {
  }

  async setFilters() {
    this.days.map(day => {
      day.selected = this.selectedDays.includes(day.name);
    })
    if (!this.days.filter(loc => loc.selected).length) this.days[0].selected = true
    
    this.locations = await this.http.get('https://sheikhalamoud.org/apis/locations/').toPromise();
    this.locations.unshift({name: 'all', uid: 0})
    this.locations.map(location => {
      location.selected = this.selectedLocations.includes(location.uid);
    });
    if (!this.locations.filter(loc => loc.selected).length) this.locations[0].selected = true
    
    this.systems = await this.http.get('https://sheikhalamoud.org/apis/systems/').toPromise();
    this.systems.unshift({name: 'all', uid: 0})
    this.systems.map(system => {
      system.selected = this.selectedSystems.includes(system.uid);
    });
    if (!this.systems.filter(loc => loc.selected).length) this.systems[0].selected = true

    this.getCourses();

    this.filtersAreReady = true;
  }
  
  async getCourses() {
    this.courses = await this.http.get('https://sheikhalamoud.org/apis/sessions/', {params: {
      after_date: this.getStartDate(),
      before_date: this.getEndDate(),
    }}).toPromise();
    this.courses.map(this.cbGroupCoursesByDays);
    this.colorsSet = true;
    this.filter();
    
    this.areCoursesReady = true;
  }

  getStartDate() {
    const m = new Date().getMonth() + 1;
    const y = new Date().getFullYear();
    return `${y}-${m}-01T00:00:00.000Z`
  }
  getEndDate() {
    let m: any = new Date().getMonth() + 2;
    m = m > 12 ? 1 : m;
    m = m > 9 ? m : "0" + m;
    const d = new Date(1, m, 0).getDate();
    const y = new Date().getFullYear() + 1;
    return `${y}-${m}-${d}T00:00:00.000Z`
  }
  
  filter = () => {
    this.days.map(day => day.courses = []);
    this.courses.map(this.cbGroupCoursesByDays);
    this.filtered = this.days[0].selected
      ? this.days.slice(1)
      : this.days.slice(1).filter(day => day.selected);
    if (this.selectedSystems.length && this.selectedSystems[0] != 0) {
      this.filtered.map(
        day => day.courses = day.courses.filter(
          course => this.selectedSystems.includes(course.System.uid)
        )
      );
    }
    if (this.selectedLocations.length && this.selectedLocations[0] != 0) {
      this.filtered.map(
        day => day.courses = day.courses.filter(
          course => this.selectedLocations.includes(course.Location.uid)
        )
      );
    }
  }

  cbGroupCoursesByDays = course => {
    if (!this.colorsSet)
      course.color = this.colors[Math.floor(Math.random()*4)];
    course['days'].map((courseDay: string) => {
      this.days.map(day => {
        if (day.name === courseDay) day.courses.push(course);
      });
    });
  }

  toggle(variable: number, idx: number) {
    let selected
    switch(variable) {
      case 0: // days
        this.handleSelectionUI(idx, this.days, 'days');
        selected = this.days.filter(day => day.selected);
        this.selectedDays = selected.map(day => day.name)
        localStorage.setItem('selectedDays', JSON.stringify(this.selectedDays));
        break;
      case 1: // systems
        this.handleSelectionUI(idx, this.systems, 'systems');
        selected = this.systems.filter(system => system.selected);
        this.selectedSystems = selected.map(system => system.uid)
        localStorage.setItem('selectedSystems', JSON.stringify(this.selectedSystems));
        break;
      case 2: // locations
        this.handleSelectionUI(idx, this.locations, 'locations');
        selected = this.locations.filter(location => location.selected);
        this.selectedLocations = selected.map(location => location.uid)
        localStorage.setItem('selectedLocations', JSON.stringify(this.selectedLocations));
        break;
      default:
        return;
    }
    this.filter();
  }

  handleSelectionUI(idx, obj, lsv) {
    if (idx == 0 && !obj[0].selected) {
      obj.map(i => i.selected = false);
      if (lsv == 'days') this.selectedDays = [];
      else if (lsv == 'systems') this.selectedSystems = [];
      else this.selectedLocations = [];
    }
    else if (obj[0].selected) {
      obj[0].selected = false;
      if (lsv == 'days') this.selectedDays = [];
      else if (lsv == 'systems') this.selectedSystems = [];
      else this.selectedLocations = [];
    }
    obj[idx].selected = !obj[idx].selected;
    if (!obj.filter(i => i.selected).length) {
      obj[0].selected = true;
    }
  }
}
