import { Component, OnInit, Input, ElementRef, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-course',
  templateUrl: './course.component.html',
  styleUrls: ['./course.component.scss']
})
export class CourseComponent implements OnInit {
  course;
  courseIsReady: boolean = false;

  constructor(private http: HttpClient) {
    this.getCourse();
  }
  
  ngOnInit() {
  }
  
  async getCourse() {
    const id = location.hash.split('/')[2];
    this.course = await this.http.get(`https://sheikhalamoud.org/apis/sessions/${id}`).toPromise();
    this.course.description = this.course.description.replace(/(?:\\[rn]|[\r\n]+)+/g, "\n").replace(/\\"/g, '"');

    this.courseIsReady = true;
  }

  getTimeFrom(d: Date) {
    const date = new Date(d);
    let hours = date.getHours();
    const minutes = date.getMinutes();
    let stringHours: string;
    let stringMinutes: string;
    const ampm = hours >= 12 ? 'م' : 'ص';
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    stringHours = hours < 10 ? '0' + hours : hours.toString();
    stringMinutes = minutes < 10 ? '0' + minutes : minutes.toString();
    var strTime = stringHours + ':' + stringMinutes + ' ' + ampm;
    return strTime;
  }

  getDateFrom(d: Date) {
    const date = new Date(d);
    const day = date.getDate();
    const month = date.toLocaleDateString('ar', {month: 'long'});
    const year = date.getFullYear();
    return day + " " + month + " " + year
  }

  open(url: string) {
    window.open(url, '_blank')
  }

}
